<template>
  <v-app>
      <v-app-bar
        v-if="display"
        app
        color="primary"
        dark
      >
        <div class="d-flex align-center" style="cursor: pointer;" @click="p('/')">
          <v-icon large class="pe-4">mdi-heart</v-icon>
          <div :class="`text-${$vuetify.breakpoint.md ? 'h4' : 'h6'}`" style='font-family: "Pacifico", cursive !important; font-weight: 400; font-style: normal;'>Felicidades Miniña</div>
        </div>
  
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-divider vertical></v-divider>
          
          <v-btn style='font-family: "Pacifico", cursive !important; font-weight: 400; font-style: normal;' text @click="p('/carta')">
            Carta
          </v-btn>
  
          <v-divider vertical></v-divider>
        </v-toolbar-items>
      </v-app-bar>
  
      <v-main id="background-container" :class="'background-container' + (display ? '' : ' align-center')">
        <template v-if="!early">
          <div v-if="!display" class="ma-auto align-center justify-center d-flex" style="max-height: min-content;max-width: 200px;box-shadow: 0px 0px 50px 5px black;border-radius: 10px;">
            <form @submit.prevent="login" class="d-flex align-center" style="width: 100%;">
              <v-text-field
                v-model="pwd"
                type="password"
                label="Contraseña"
                class="mx-5"
                :error="!!errorMsg"
                :error-messages="errorMsg"
                @keyup.enter="login"
              ></v-text-field>

              <v-btn
                color="primary"
                class="me-5"
                small
                type="submit"
                fab
              >
                <v-icon>mdi-send</v-icon>
              </v-btn>
            </form>
          </div>
          <router-view v-else
            :key="$route.fullPath" id="background" 
            class="background" 
            @zoom="openZoom"/>
        </template>
        <template v-else>
          <div style="height: 50vh;" class="d-flex flex-wrap justify-center">
            <div style="width: 51%" class="d-flex justify-center align-center primary--text"><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">DÍAS: </h1><v-spacer/><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">{{ this.remaining.d }}</h1></div>
            <div style="width: 51%" class="d-flex justify-center align-center primary--text"><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">HORAS: </h1><v-spacer/><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">{{ this.remaining.h }}</h1></div>
            <div style="width: 51%" class="d-flex justify-center align-center primary--text"><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">MINUTOS: </h1><v-spacer/><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">{{ this.remaining.m }}</h1></div>
            <div style="width: 51%" class="d-flex justify-center align-center primary--text"><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">SEGUNDOS: </h1><v-spacer/><h1 :style="`font-size: ${(this.$vuetify.breakpoint.mdAndUp ? 4 : 3)}rem; text-shadow: 3px 3px black;`">{{ this.remaining.s }}</h1></div>
          </div>
        </template>
      </v-main>
      <zoom-image ref="zoomImage"></zoom-image>
  </v-app>
</template>

<script>
import Trianglify from "trianglify";
import bcrypt from "bcryptjs";

export default {
  components: {
    ZoomImage: () => import('@/components/ZoomImage.vue'),
  },
  name: 'App',
  data() {
    return{
      remaining:{
        d:0,
        h:0,
        m:0,
        s:0,
      },
      bd: new Date(2025, 11, 24, 0, 0, 0 ,0),
      svg: {},
      display: false,
      early: true,
      pwd: '',
      errorMsg: '',
      hash: '$2a$12$L1.gGaM1FfZnG8IiGnLIzO9hoxbLFDgmJSABphUIoVaRyiaGEHZzK'
    }
  },
  methods:{
    updateTimer() {
      const now = new Date().getTime();
      const distance = this.bd.getTime() - now;

      if (distance <= 0) {
        this.remaining = { d: 0, h: 0, m: 0, s: 0 };
        clearInterval(this.timer);
        this.early = false;
        return;
      }

      const days = Math.floor(distance / (1000 * 60 * 60 * 24));
      const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((distance % (1000 * 60)) / 1000);

      this.remaining = { d: days, h: hours, m: minutes, s: seconds };
    },
    p(path){
      this.$router.push({ path: path }).catch(()=>{});
    },
    async login() {
      try {
        const ok = await bcrypt.compare(this.pwd, this.hash);
        if (ok) {
          this.display = true;
          this.errorMsg = '';
        } else {
          this.errorMsg = 'Nuh uh';
        }
      } catch (err) {
        console.error("Error verificando contraseña:", err);
        this.errorMsg = 'Si ves esto avisame que no debería salirte este error :D';
      }
    },
    openZoom(event) {
      this.$refs.zoomImage.open(event);
    },
    svgNew() {
      const pattern = Trianglify({
        width: window.innerWidth,
        height: window.innerHeight,
        cellSize: 75,
        variance: 0.75,
        xColors: ['#f893b2ff', '#ffffff', '#f893b2ff'],
      });

      this.svg.width = window.innerWidth;
      this.svg.height = window.innerHeight;
      this.svg.pattern = pattern;

      const canvas = this.svg.pattern.toCanvas();
      const dataUrl = canvas.toDataURL();

      const bg = document.getElementById("background-container");
      if (bg) {
        bg.style.background = `url(${dataUrl})`;
        bg.style.height = `100vh`;
        bg.style.width = `100vw`;
      }
    },
    onResize() {
      this.svg.width = window.innerWidth;
      this.svg.height = window.innerHeight;
      this.svgDraw("resize", this.svg);
    },
    svgDraw(r, s) {
      if (r === "resize"){
        const pattern = s.pattern
  
        const canvas = pattern.toCanvas();
        const dataUrl = canvas.toDataURL();
  
        const bg = document.getElementById("background-container");
        if (bg) {
          bg.style.width = `${window.innerWidth}px`;
          bg.style.height = `${window.innerHeight}px`;
          bg.style.background = `url(${dataUrl})`;
          bg.style.backgroundSize = 'cover';
        }
      }
      else{
        const pattern = Trianglify({
          width: window.innerWidth,
          height: window.innerHeight,
          cellSize: 75,
          variance: 0.75,
        });
  
        const canvas = pattern.toCanvas();
        const dataUrl = canvas.toDataURL();
  
        const bg = document.getElementById("background-container");
        if (bg) {
          bg.style.width = `${window.innerWidth}px`;
          bg.style.height = `${window.innerHeight}px`;
          bg.style.background = `url(${dataUrl})`;
          bg.style.backgroundSize = 'cover';
        }
      }
    },
    updateSizes() {
      const bgContainer = document.getElementById("background-container");
      if (bgContainer) {
        bgContainer.style.minWidth = window.innerWidth;
        bgContainer.style.minHeight = window.innerHeight;
      }
    },
  },
  mounted(){
    this.svgNew();
    window.addEventListener("resize", this.onResize);
    if (new Date() >= this.bd) {
      this.early = false
      document.title = 'Cumpleaños de Miniña';
    }
    else {
      this.updateTimer();
      this.timer = setInterval(this.updateTimer, 1000);
      document.title = '???????????';
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

<style>
@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

.background-container{
  position: relative;
  z-index: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
</style>