import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
		component: () => import('@/views/MainIndex.vue'),
  },
  {
    path: '/carta',
    name: 'carta',
		component: () => import('@/views/CartaAmor.vue'),
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router